@use "../utils";
.wp-block-pullquote {
	padding: 0;
	margin-bottom: 0;
	text-align: left;
	&.is-style-small{
		height: auto;
		display: block;
		blockquote{
			border-left: 3px solid var(--color-borders);
			padding: 0 0 0 1em;
			p{
				font-family: var(--font-family-base);
				color: var(--color-text);
				font-size: var(--small-text);
				line-height: var(--line-height-base);
				&::before,
				&::after{
					display: none;
				}
			}
		}
	}
	&.has-text-align-center{
		p:before{
			position: static;
		}
	}
	.wp-block-bespoke-column &:not(.is-style-small) {
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	blockquote {
		display: flex;
		flex-direction: column;
		gap: #{utils.makeClamp(20, 30)};
		max-width: 1170px;
		margin-inline: auto;
		@media (max-width: #{utils.getBreakpoint('md', true)}){
			padding: 0 0 50px;
		}
	}
	p {
		font-family: var(--font-family-headings);
		font-size: var(--font-size-h2);
		letter-spacing: var(--narrow-letter-spacing);
		color: var(--color-blue-1);
		line-height: 1.1;
		margin: 0;
		position: relative;

		&:before {
			content: open-quote;
			left: -0.6em;
			position: absolute;
		}
		&:after {
			content: close-quote;
			display: inline-block;
			transform: translateX(-0.25em);
		}
	}
	cite {
		font-size: var(--small-text);
		color: var(--color-blue-2);
		display: block;
		font-style: normal;
		line-height: 1.2;
		strong {
			font-size: var(--font-size-h6);
			color: var(--color-blue-1);
		}
	}
}
