@use "sass:map";
@use "sass:math";

$grid-breakpoints: (
	xs: 0,
	ms: 375px,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
	xxl: 1500px,
);

@function rem($pxValue) {
	@return #{math.div($pxValue, 16)}rem;
}

@function makeClamp($min, $max) {
	@return clamp(#{rem($min)}, #{math.div($max, 1600) * 100}vw, #{rem($max)});
}

@function getBreakpoint($key, $asMaxValue: false) {
	@if $asMaxValue {
		@return map-get($grid-breakpoints, $key) - 1;
	}
	@return map-get($grid-breakpoints, $key);
}

@mixin resetList {
	list-style: none;
	margin: 0;
	padding: 0;
}

@mixin resetButton {
	appearance: none;
	background: transparent;
	border-radius: 0;
	border: 0;
	padding: 0;
	cursor: pointer;
	display: inline-block;
	font-family: inherit;
}

@mixin resetField {
	background: transparent;
	border: 0;
	border-radius: 0;
	padding: 0;
	margin: 0;
	appearance: none;
}

@mixin btnBase {
	border: solid 3px var(--color-buttons);
	color: var(--color-buttons);
	display: inline-block;
	font-family: var(--font-family-semi-bold);
	font-size: var(--font-size-buttons);
	font-weight: 500;
	line-height: 1;
	padding: 1em 1.39em;
	transition: all var(--transitions);
	user-select: none;
	white-space: nowrap;
	text-align: center;
	text-decoration: none;
	@media (hover: hover) {
		&:hover {
			text-decoration: none;
			border-color: var(--color-buttons-hover);
			color: var(--color-buttons-hover);
			background-color: var(--color-buttons-bg-hover);
		}
	}
	&:not(:disabled) {
		cursor: pointer;
	}
	&:disabled {
		opacity: 0.5;
	}
}

@mixin btnSecondary {
	border: none;
	border-top: solid 3px var(--color-buttons);
	padding: 6px 0 0;
	@media (hover: hover) {
		&:hover {
			color: var(--color-buttons-hover, var(--color-blue-1)); 
			border-top-color: var(--color-buttons-hover, var(--color-blue-1)); 
			background: none;
		}
	}
}

@mixin fieldBase {
	padding: var(--field-padding);
	background: var(--field-color, var(--color-white));
	color: var(--field-text-color, var(--color-text));
	min-height: 60px;
	width: 100%;
	font-size: rem(16);
	font-family: var(--font-family-base);

	&:focus{
		box-shadow: 0 0 6px rgba(0,80,158,.5);
	}
}

@mixin choiceLabel {
	display: flex;
	align-items: center;
	gap: 5px;
	margin: 0;
	font-size: var(--small-text);
	font-family: var(--font-family-base);
	position: relative;
	&::before {
		content: "";
		width: 30px;
		aspect-ratio: 1;
		background: var(--field-color, var(--color-white));
	}
}

@mixin choiceInput {
	position: absolute;
	width: 30px;
	aspect-ratio: 1;
	opacity: 0;
	left: 0;
}

@mixin checkboxLabelBase {
	&::before {
		background-repeat: no-repeat;
		background-size: 50% auto;
		background-position: center center;
	}
}
@mixin checkboxLabelChecked {
	&::before {
		background-image: url("/assets/images/checkbox-check.svg");
	}
}

@mixin radioLabelBase {
	&::before {
		border-radius: 50%;
	}
	&::after {
		content: "";
		position: absolute;
		width: 10px;
		aspect-ratio: 1;

		border-radius: 50%;
		top: 15px;
		left: 15px;
		transform: translate(-50%, -50%);
	}
}
@mixin radioLabelChecked {
	&::after {
		background: var(--color-blue-1);
	}
}

@mixin accent($size: "normal", $location: "above") {
	@if $location == "below" {
		&::after {
			content: "";
			display: block;
			background: var(--color-accent);
			height: 3px;
			margin-top: 0.75em;
			@if $size == "full" {
				width: 100%;
			} @else {
				width: clamp(#{rem(80)}, 4.5vw, #{rem(90)});
			}
		}
	} @else {
		&::before {
			content: "";
			display: block;
			background: var(--color-accent);
			height: 3px;
			margin-bottom: clamp(#{rem(6)}, 1vw, #{rem(8)});
			@if $size == "full" {
				width: 100%;
			} @else {
				width: clamp(#{rem(80)}, 4.5vw, #{rem(90)});
			}
		}
	}
}

// https://www.a11yproject.com/posts/how-to-hide-content/
@mixin visually-hidden {
	clip: rect(0 0 0 0);
	clip-path: inset(50%);
	height: 1px;
	overflow: hidden;
	position: absolute;
	white-space: nowrap;
	width: 1px;
}

@mixin dropdownBase {
	border-radius: 0.5rem;
	box-shadow: 0 9px 80px rgba(0, 0, 0, 0.12), 0 2px 18px rgba(0, 0, 0, 0.07),
		0 1px 5px rgba(0, 0, 0, 0.05);
	flex-direction: column;
	min-width: #{makeClamp(150, 250)};
	padding: 1.3rem 0;
	position: absolute;
}
@mixin dropdownLink {
	color: var(--color-black);
	display: block;
	padding: #{makeClamp(2, 8)} #{makeClamp(20, 30)};
	text-decoration: none;
	&:hover {
		background: var(--color-blue-5);
	}
}
@mixin focusStyle {
	outline-color: var(--color-blue-1);
	outline-style: solid;
	outline-width: 2px;
}

// this is one big mixin because two almost identical blocks use the same UI
@mixin timelineStyles{
	.swiper {
		&::before,
		&::after {
			content: "";
			position: absolute;
			height: 100%;
			width: 33%;
			top: 0;
			z-index: 5;
			@media (max-width: #{getBreakpoint('lg', true)}) {
				display: none;
			}
		}
		&::before {
			left: 0;
			background: linear-gradient(
				270deg,
				rgba(255, 255, 255, 0) 0%,
				rgba(255, 255, 255, 1) 100%
			);
		}
		&::after {
			right: 0;
			background: linear-gradient(
				90deg,
				rgba(255, 255, 255, 0) 0%,
				rgba(255, 255, 255, 1) 100%
			);
		}
	}
	.slider-control {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		display: flex;
		gap: 15px;
		align-items: center;
		z-index: 10;
		width: auto;
		border: 0;
		color: var(--color-blue-1);
		@media (max-width: #{getBreakpoint('lg', true)}) {
			color: var(--color-white);
		}
		&.next {
			right: 0;
		}
		.icon {
			display: grid;
			place-content: center;
			width: 60px;
			height: 60px;
			border: solid 3px var(--color-blue-1);
			@media (max-width: #{getBreakpoint('lg', true)}) {
				border-color: var(--color-white);
			}
		}
		.label {
			font-size: var(--font-size-h6);
			font-family: var(--font-family-semi-bold);
			@media (max-width: #{getBreakpoint('lg', true)}) {
				display: none;
			}
		}
	}
	.swiper-slide {
		img {
			transition: opacity var(--transitions);
			opacity: 0.5;
		}
	}
	.swiper-slide-active {
		img {
			opacity: 1;
		}
	}

	.timeline-navigation {
		display: flex;
		scroll-behavior: smooth;
		padding-inline: calc(
			50% - calc(var(--timeline-navigation-item-width) * 0.5)
		);
		padding-block: var(--spacing-4) var(--spacing-2);
		overflow-x: scroll;
		position: relative;
		button {
			text-align: center;
			min-width: var(--timeline-navigation-item-width);
			color: var(--color-blue-1);
			font-family: var(--font-family-headings);
			font-size: var(--font-size-h3);
			text-wrap: nowrap;
			display: grid;
			gap: 7px;
			opacity: 0.5;
			place-content: center;
			&.active {
				opacity: 1;
				color: var(--color-text);
				.title::after {
					width: 100%;
				}
				.label {
					color: var(--color-red);
				}
			}
			.label {
				font-size: var(--font-size-h6);
				font-family: var(--font-family-semi-bold);
			}
			.title{
				position: relative;
				padding-bottom: 10px;
				&::after {
					content: "";
					position: absolute;
					left: 50%;
					transform: translateX(-50%);
					bottom: 0;
					height: 2px;
					background: var(--color-red);
					width: 0;
					transition: width var(--transitions);
					transition-delay: 0.3s;
				}
			}
		}
	}

	.timeline-content {
		max-width: 61ch;
		text-align: center;
		margin-inline: auto;
		position: relative;
		padding-top: var(--spacing-2);
		.item {
			position: absolute;
			opacity: 0;
			top: var(--spacing-2);
			pointer-events: none;
			transition: opacity var(--transitions);
			&.active {
				display: block;
				position: static;
				opacity: 1;
				pointer-events: initial;
			}

			> *:first-child {
				margin-top: 0;
			}
			> *:last-child {
				margin-bottom: 0;
			}
		}
	}
}