@use "../utils";
table{
	width: 100%;
	border-collapse: collapse;
	thead{
		th{
			color: var(--color-red);
			button{
				color: currentColor;
				font-size: 1em;
				display: inline-flex;
				align-items: center;
				gap: .25em;
				&.asc,
				&.desc{
					&::after{
						content: '';
						width: 10px;
						height: 13px;
						background: url('/assets/images/arrow-red.svg') no-repeat;
						background-size: 100% auto;
					}
				}
				&.asc{
					&::after{
						transform: rotate(180deg);
					}
				}
			}
		}
	}
	th, td{
		border-bottom: solid 1px var(--color-blue-4);
		text-align: left;
		padding-block: 1.4em;
	}
	th{
		font-weight: 400;
		font-family: var(--font-family-semi-bold);
	}
	@media (max-width: #{utils.getBreakpoint('lg', true)}){
		font-size: #{utils.rem(12)};
		th, td{
			padding-inline: .5em;
		}
	}
}