@use "../utils";
.list-dividers {
	@include utils.resetList;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;

	li {
		line-height: 1.4;

		&:not(:last-child) {
			border-right: solid var(--color-blue-2) 1px;
		}
	}
	a {
		padding: 0 1em;
	}
}
.is-style-icon {
	@include utils.resetList;
	display: grid;
	gap: 1em;
	li {
		display: flex;
		gap: 1em;
		align-items: center;
		font-family: var(--font-family-semi-bold);
		color: var(--color-blue-1);
	}
}
