@use "../utils";

/* thanks, tribe devs */
$heading-top-spacing: .9em;
$heading-bottom-spacing: .36em;

h1,
h2,
h3,
h4,
h5 {
	font-family: var(--font-family-headings);
	font-style: normal;
	font-weight: 400;
	letter-spacing: var(--narrow-letter-spacing);
	line-height: 1.1;
	margin-top: $heading-top-spacing;
	margin-bottom: 0.36em;
	color: var(--color-blue-1);
}


.tribe-common{
	h1,
	h2,
	h3,
	h4,
	h5 {
		margin-top: $heading-top-spacing;
		margin-bottom: $heading-bottom-spacing;
	}
	.has-text-align-center{
		margin-inline: auto;
	}
}

.is-style-micro,
.heading-micro {
	font-family: var(--font-family-sans-serif);
	font-style: normal;
	font-weight: 400;
	font-size: #{utils.makeClamp(13, 14)};
	line-height: 1.6;
	text-transform: uppercase;
}

.wp-block-heading,
[class^="heading-"] {
	display: block;
	hanging-punctuation: first last;
	transition: color var(--transitions);
	@media (min-width: #{utils.getBreakpoint('lg')}){
		text-wrap: balance;
	}
}

/*
.has-h- classes not added on h1-h4 because wp already handles that 
h5 and h6 have extra styling rules, so we're including the class there
*/
h1 {
	font-size: var(--font-size-h1);
}

h2 {
	font-size: var(--font-size-h2);
}
h3 {
	font-size: var(--font-size-h3);
}
h4 {
	font-size: var(--font-size-h4);
	text-wrap: initial;
}
h5,
.has-h-5-font-size {
	font-size: var(--font-size-h5);
	line-height: 1.3;
	text-wrap: initial;
}
h6,
.has-h-6-font-size {
	font-family: var(--font-family-semi-bold);
	font-style: normal;
	font-weight: 400;
	font-size: var(--font-size-h6);
	line-height: 1.1;
	text-wrap: initial;
}


.block-header {
	align-items: flex-end;
	display: flex;
	flex-wrap: wrap;
	gap: 15px;
	justify-content: space-between;
	padding-bottom: var(--spacing-3);
	margin-bottom: var(--spacing-4);
	position: relative;
	border-bottom: solid 1px var(--color-blue-4);

	h1,
	h2 {
		margin: 0;
	}
	&.is-style-minimal {
		border: 0;
		padding: 0;
		margin-bottom: var(--spacing-3);
	}

	.nav-link {
		color: var(--color-blue-1);
	}
}
// make the core block look like .block-header
.wp-block-heading.is-style-block-heading {
	border-bottom: solid 1px var(--color-blue-4);
	color: var(--color-red);
	padding-bottom: var(--spacing-3);
	margin-bottom: var(--spacing-4);
}
.block-header-title {
	margin: 0;
	color: var(--color-red);
	flex: 1;
}
.block-header-link {
	color: var(--color-red);
	text-decoration: none;
	&:hover{
		text-decoration: underline;
	}
}

.article-header {
	text-align: center;
	padding-block: var(--spacing-3);
	border-bottom: solid 1px var(--color-blue-4);
	h1 {
		text-wrap: balance;
		margin-bottom: 5px;
		max-width: 40ch;
		margin-inline: auto;
		margin-top: 0;
		&::before {
			margin-inline: auto;
		}
	}
	h2 {
		margin-top: 0;
		color: var(--color-red);
	}
	.social {
		align-items: center;
		display: inline-flex;
		flex-wrap: wrap;
		gap: #{utils.makeClamp(15, 20)};
		list-style: none;
		padding-top: #{utils.makeClamp(15, 20)};
		border-top: solid 1px var(--color-blue-4);
		margin-block: var(--spacing-2) var(--spacing-3);
		.heading {
			color: var(--color-social-title);
		}
		a {
			color: var(--color-anchors);
		}
	}
	.btn-row {
		margin-top: var(--spacing-3);
	}
}
.header-wrap {
	display: grid;
	position: relative;

	.link-back {
		@media (max-width: #{utils.getBreakpoint('lg', true)}) {
			margin-bottom: 3rem;
		}
		@media (min-width: #{utils.getBreakpoint('lg')}) {
			left: 0;
			position: absolute;
			top: 50%;
		}
	}
}
