@use "../utils";

.btn,
.wp-element-button {
	@include utils.resetButton;
	@include utils.btnBase;
}

// .btn-primary {
// 	@include utils.btnPrimary;
// }

.wp-block-button.is-style-secondary .wp-element-button,
.btn-secondary {
	@include utils.btnSecondary;
}

.wp-block-buttons {
	gap: clamp(15px, 1vw, 20px);
	@media (max-width: #{utils.getBreakpoint('sm', true)}) {
		.wp-block-button:not(.is-style-secondary),
		.wp-element-button {
			width: 100%;
		}
	}
}

.link-back {
	color: var(--color-blue-2);
	display: inline-flex;
	font-size: #{utils.makeClamp(13, 16)};
	line-height: 1.1;
	gap: 0.4em;
	align-items: center;
	text-decoration: none;
	&:hover{
		svg{
			transform: translateX(-5px);
		}
	}

	svg{
		transition: var(--transitions);
	}
}

.btn-row {
	@include utils.resetList;
	align-items: center;
	display: inline-flex;
	flex-wrap: wrap;
	gap: var(--spacing-2);
	list-style: none;
}
