@use "../utils";
:root {
	--color-blue-1: #00509e;
	--color-blue-2: #427ab1;
	--color-blue-25: #759EC6;
	--color-blue-3: #9fbcd9;
	--color-blue-4: #c6d8e8;
	--color-blue-5: #ecf2f8;
	--color-red: #a30b35;
	--color-black: #000;
	--color-white: #fff;
	--color-gray: #808080;
	--color-body: #fff;
	--color-anchors: var(--color-black);
	--color-text: var(--color-black);
	--color-accent: var(--color-red);
	--color-toggle: var(--color-black);
	--color-buttons: var(--color-red);
	--color-buttons-hover: var(--color-blue-1);
	--color-buttons-bg-hover: var(--color-blue-5);
	--color-social-title: var(--color-blue-2);
	--color-borders: var(--color-blue-4);
	--color-accordion-borders: var(--color-blue-3);
	--color-accordion-indicator: var(--color-blue-2);
	--state-outline-color: var(--color-white);
	--vw-max-width: 89.74vw;	--content-max-width: min(var(--vw-max-width), 690px);
	--wide-max-width: min(var(--vw-max-width), 1410px);
	// --container-max-width: min(var(--vw-max-width), 1410px);
	--field-color: #fff;
	--field-padding: 1.1875em;
	--field-spacing: 1em;
	--font-family-base: "Inter-Regular", sans-serif;
	--font-family-semi-bold: "Inter-SemiBold", sans-serif;
	--font-family-headings: "goodchild-pro", sans-serif;
	--font-family-sans-serif: "Inter-Regular", sans-serif;
	--font-size-base: #{utils.makeClamp(16, 18)};
	--font-size-buttons: #{utils.makeClamp(14, 18)};
	--line-height-base: 1.6;
	--gap: #{utils.makeClamp(20, 30)};
	--gutter: 20px;
	--site-header-height: 92px;
	--transitions: 0.3s;

	--font-size-h1: #{utils.makeClamp(25, 55)};
	--font-size-h2: #{utils.makeClamp(23, 40)};
	--font-size-h3: #{utils.makeClamp(22, 32)};
	--font-size-h4: #{utils.makeClamp(20, 28)};
	--font-size-h5: #{utils.makeClamp(18, 22)};
	--font-size-h6: #{utils.makeClamp(16, 18)};
	--small-text: #{utils.makeClamp(13, 14)};
	--large-text: #{utils.makeClamp(16, 20)};
	--oversized-text: #{utils.makeClamp(40, 90)};
	--narrow-letter-spacing: -0.02em;

	--spacing-1: #{utils.makeClamp(8, 10)};
	--spacing-2: #{utils.makeClamp(15, 20)};
	--spacing-3: #{utils.makeClamp(20, 30)};
	--spacing-4: #{utils.makeClamp(40, 50)};
	--spacing-5: #{utils.makeClamp(50, 75)};
	--spacing-6: #{utils.makeClamp(50, 100)};
	--spacing-7: #{utils.makeClamp(60, 120)};
	--spacing-8: #{utils.makeClamp(60, 150)};
	--spacing-9: #{utils.makeClamp(60, 200)};
	--spacing-column-padding: 50px 20px;

	--swiper-pagination-bullet-height: 15px;
	--swiper-pagination-bullet-inactive-color: transparent;
	--swiper-pagination-color: transparent;
	--swiper-pagination-bullet-horizontal-gap: 0;
	--swiper-pagination-bullet-size: #{utils.makeClamp(40, 90)};
	--swiper-pagination-bullet-border-radius: 0;
	--swiper-pagination-bullet-inactive-opacity: 1;

	--timeline-navigation-item-width: 300px;

	@media (min-width: #{utils.getBreakpoint('lg')}){
		--site-header-height: 175px;
		--spacing-column-padding: 17.39%;
	}

}

body{
	--wp--style--global--content-size: min(var(--vw-max-width), 690px);
}