@use "../utils";
figure.aligncenter {
	display: flex;
	justify-content: center;
}
figure.alignright {
	display: flex;
	justify-content: flex-end;
}

.has-text-align-left {
	text-align: left;
}
.has-text-align-center {
	text-align: center;
	&::before,
	&::after {
		margin-inline: auto;
	}
}
.has-text-align-right {
	text-align: center;
}

.is-content-justification-left {
	justify-content: flex-start;
}
.is-content-justification-center {
	justify-content: center;
}
.is-content-justification-right {
	justify-content: flex-end;
}
.is-content-justification-space-between {
	justify-content: space-between;
}

.is-style-accent {
	@include utils.accent;
}
.is-style-accent-full {
	@include utils.accent("full");
}
.is-style-accent-below {
	@include utils.accent("normal", "below");
	&.has-oversized-font-size{
		&::after{
			margin-top: .5em;
		}
	}
}

.is-style-global-bg {
	background-image: url("/assets/images/backgrounds/bg-1-lg.png");
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	background-color: var(--color-blue-1);
	color: var(--color-white);
	--color-accent: var(--color-white);
	.block-header-title,
	.wp-block-heading {
		color: currentColor;
	}
}

.text-small {
	font-size: var(--small-text);
}
.text-large {
	font-size: var(--large-text);
}

.has-white-color,
.has-blue-color,
.has-blue-2-color,
.has-red-color {
	--color-accent: currentColor;
}

.has-blue-2-background-color,
.has-blue-background-color,
.has-red-background-color {
	--color-accent: var(--color-white);
	--color-anchors: var(--color-white);
	--color-buttons: var(--color-white);
	--color-buttons-hover: var(--color-white);
	--color-buttons-bg-hover: rgba(255,255,255,.1);
	--color-toggle: var(--color-white);
	--color-borders: rgba(255,255,255,.2);
	--color-accordion-borders: var(--color-blue-2);
	--color-accordion-indicator: var(--color-blue-3);
	--color-social-title: var(--color-blue-3);
	color: var(--color-white);
	h1,
	h2,
	h3,
	h4,
	h5,
	label,
	.wp-block-heading {
		color: currentColor;
	}
}
.has-blue-background-color {
	--mask-color: var(--color-blue-1);
}
.has-blue-5-background-color {
	--mask-color: var(--color-blue-5);
}
.has-red-background-color {
	--mask-color: var(--color-red);
}

.align-self-end {
	align-self: flex-end;
}

p,
ul,
ol {
	&.has-background {
		padding: 1em;
	}
}

.hidden-xs{
	@media (max-width: #{utils.getBreakpoint('sm', true)}){
		display: none;
	}
}
