@use "../utils";
.article-grid {
	display: grid;
	gap: calc(var(--gap) * 2) var(--gap);
	margin-bottom: var(--spacing-3);
	@media (min-width: #{utils.getBreakpoint('md')}) {
		grid-template-columns: repeat(2, 1fr);
	}
	@media (min-width: #{utils.getBreakpoint('lg')}) {
		grid-template-columns: repeat(3, 1fr);
	}
}
